import React, { useState } from 'react';
import { StyleTypography } from '../styles';
import { Grid, Stack } from '@mui/material';
import RadioButtonVT from '../../../components/radioButton/radioButton';
import TextFieldVT from '../../../components/textField/textField';

interface SelectedValues {
  comorbidade: string;
  alcool: string;
  medicamento: string;
  droga: string;
  cirurgia: string;
  alergia: string;
}
const TriagemHistory = () => {
  const [selectedValue, setSelectedValues] = useState<SelectedValues>({
    comorbidade: '',
    alcool: '',
    medicamento: '',
    droga: '',
    cirurgia: '',
    alergia: '',
  });

  const handleRadioButtonChange = (
    group: keyof SelectedValues,
    value: string
  ) => {
    setSelectedValues(prevValues => ({
      ...prevValues,
      [group]: value,
    }));
  };

  return (
    <div>
      <Stack direction={'row'} spacing={10} style={{ paddingBottom: '3rem' }}>
        <Grid>
          <StyleTypography variant='subtitle1'>
            Voc possui alguma comorbidade?
          </StyleTypography>
          <Stack direction={'row'}>
            <RadioButtonVT
              label='Sim'
              value='Sim comorbidade'
              checked={selectedValue.comorbidade === 'Sim comorbidade'}
              onChange={value => handleRadioButtonChange('comorbidade', value)}
            />

            <RadioButtonVT
              label='No'
              value='No comorbidade'
              checked={selectedValue.comorbidade === 'No comorbidade'}
              onChange={value => handleRadioButtonChange('comorbidade', value)}
            />
          </Stack>
          <TextFieldVT
            model='tertiary'
            label='Se sim, qual(is)?'
            multiline
            maxRows={3}
          />
        </Grid>
        <Grid>
          <StyleTypography variant='subtitle1'>
            Voc consome bebidas alcolicas?
          </StyleTypography>
          <Stack direction={'row'}>
            <RadioButtonVT
              label='Sim'
              value='Sim alcool'
              checked={selectedValue.alcool === 'Sim alcool'}
              onChange={value => handleRadioButtonChange('alcool', value)}
            />

            <RadioButtonVT
              label='No'
              value='No alcool'
              checked={selectedValue.alcool === 'No alcool'}
              onChange={value => handleRadioButtonChange('alcool', value)}
            />
          </Stack>
          <TextFieldVT
            model='tertiary'
            label='Se sim, com qual frequncia?'
            multiline
            maxRows={3}
          />
        </Grid>
        <Grid>
          <StyleTypography variant='subtitle1'>
            Voc faz uso de algum medicamento?
          </StyleTypography>
          <Stack direction={'row'}>
            <RadioButtonVT
              label='Sim'
              value='Sim medicamento'
              checked={selectedValue.medicamento === 'Sim medicamento'}
              onChange={value => handleRadioButtonChange('medicamento', value)}
            />

            <RadioButtonVT
              label='No'
              value='No medicamento'
              checked={selectedValue.medicamento === 'No medicamento'}
              onChange={value => handleRadioButtonChange('medicamento', value)}
            />
          </Stack>
          <TextFieldVT
            model='tertiary'
            label='Se sim, qual(is)?'
            multiline
            maxRows={3}
          />
        </Grid>
      </Stack>
      <Stack direction={'row'} spacing={10}>
        <Grid>
          <StyleTypography variant='subtitle1'>
            Voc faz uso de droga lcita ou no?
          </StyleTypography>
          <Stack direction={'row'}>
            <RadioButtonVT
              label='Sim'
              value='Sim droga'
              checked={selectedValue.droga === 'Sim droga'}
              onChange={value => handleRadioButtonChange('droga', value)}
            />

            <RadioButtonVT
              label='No'
              value='No droga'
              checked={selectedValue.droga === 'No droga'}
              onChange={value => handleRadioButtonChange('droga', value)}
            />
          </Stack>
          <TextFieldVT
            model='tertiary'
            label='Se sim, com qual frequncia?'
            multiline
            maxRows={3}
          />
        </Grid>
        <Grid>
          <StyleTypography variant='subtitle1'>
            Voc j fez alguma cirurgia?
          </StyleTypography>
          <Stack direction={'row'}>
            <RadioButtonVT
              label='Sim'
              value='Sim cirurgia'
              checked={selectedValue.cirurgia === 'Sim cirurgia'}
              onChange={value => handleRadioButtonChange('cirurgia', value)}
            />

            <RadioButtonVT
              label='No'
              value='No cirurgia'
              checked={selectedValue.cirurgia === 'No cirurgia'}
              onChange={value => handleRadioButtonChange('cirurgia', value)}
            />
          </Stack>
          <TextFieldVT
            model='tertiary'
            label='Se sim, qual(is)?'
            multiline
            maxRows={3}
          />
        </Grid>
        <Grid>
          <StyleTypography variant='subtitle1'>
            Voc tem alguma alergia?
          </StyleTypography>
          <Stack direction={'row'}>
            <RadioButtonVT
              label='Sim'
              value='Sim alergia'
              checked={selectedValue.alergia === 'Sim alergia'}
              onChange={value => handleRadioButtonChange('alergia', value)}
            />

            <RadioButtonVT
              label='No'
              value='No alergia'
              checked={selectedValue.alergia === 'No alergia'}
              onChange={value => handleRadioButtonChange('alergia', value)}
            />
          </Stack>
          <TextFieldVT
            model='tertiary'
            label='Se sim, qual(is)?'
            multiline
            maxRows={3}
          />
        </Grid>
      </Stack>
    </div>
  );
};

export default TriagemHistory;
